    if (!sign1 || !sign2) return

    setLoading(true)
    try {
      await new Promise(resolve => setTimeout(resolve, 3000))

      const compatibility = generateAICompatibility(sign1, sign2)
      setResult(compatibility)
    } catch (error) {
      console.error('Error generating compatibility:', error)
    } finally {
      setLoading(false)
    }
